import firebase from 'firebase/app';

export default{

	actions: {
		async fetchCategory(ctx, {title, limit}){
			try{
			  const uid = await ctx.dispatch('getUserId')
		      const category = await firebase.database().ref(`users/${uid}/categories`).push({title, limit}) //если в базе данных нет поля categories оно будет создано
		  		//firebase вернет категорию, в нем будет поле key
		  		return {title, limit, id: category.key}//возвращаем категорию с выдп=анным 'key', присвоив его полю id

			}catch(e){
				ctx.commit('setError', e)
				throw e
			}

	  },
	  async updateCategory(ctx, {title, limit, id}){
	  	try{
			  	const uid = await ctx.dispatch('getUserId')
		      const category = await firebase.database().ref(`users/${uid}/categories`).child(id).update({title, limit}) //обновление полей в категории с переданным id
		  		
			}catch(e){
				ctx.commit('setError', e)
				throw e
			}
	  },
	  async getCategories(ctx){
	  	try{
		  	const uid = await ctx.dispatch('getUserId')
		  	const categories = (await firebase.database().ref(`/users/${uid}/categories`).once('value')).val() || {}
		  	return Object.keys(categories).map((key) => ({...categories[key], id: key}))
		  	// return categories 
	  	}catch(e){
	  		ctx.commit('setError', e)
	  		throw e
	  	}
	  },
	  async getCategoryById(ctx, id){
	  	try{
		  	const uid = await ctx.dispatch('getUserId')
		  	const category = (await firebase.database().ref(`/users/${uid}/categories`).child(id).once('value')).val() || {}
		  	return {...category, id}
		  	// return categories 
	  	}catch(e){
	  		ctx.commit('setError', e)
	  		throw e
	  	}
	  }
	},
	getters: {
		getCats(state, actions){
			return actions.getCategories
		}
	}
}