import Vue from 'vue'
import Vuelidate from 'vuelidate'
import Paginate from 'vuejs-paginate'//плагин пагинации
import VueMeta from 'vue-meta'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import messagePlugin from '@/utils/message.plugin' //импорт самодельного рлагина
import Loader from '@/components/app/Loader'
import currencyFilter from '@/filters/currency.filter'
import dateFilter from '@/filters/date.filter'
import localizeFilter from '@/filters/localize.filter'
import tooltipDirective from '@/directives/tooltip.directive' //импорт самодельной директивы
import 'materialize-css/dist/js/materialize.min'
import titlePlugin from '@/utils/title.plugin'

import firebase from 'firebase/app';
import 'firebase/auth';
import 'firebase/database';

Vue.config.productionTip = false
Vue.use(Vuelidate) //осле подключения плагина в каждом компоненте можно использовать объект 'validations'
Vue.use(messagePlugin) //подключение самодельного рлагина
Vue.use(titlePlugin) //подключение самодельного рлагина
Vue.component('Loader', Loader) //глобальная регистрация компонента. Теперь он доступен во всем приложении
Vue.filter('currencyFilter', currencyFilter)//глобальная регистрация фильтра
Vue.filter('dateFilter', dateFilter)//глобальная регистрация фильтра
Vue.filter('localizeFilter', localizeFilter)//глобальная регистрация фильтра
Vue.directive('tooltip', tooltipDirective)//глобальная регистрация директивы
Vue.component('Paginate', Paginate)
Vue.use(VueMeta, {
  refreshOnceOnNavigation: true
})

var firebaseConfig = {
    apiKey: "AIzaSyBvxvk7od8Mmdrxe_SN3urtmQArZx-Dyb8",
    authDomain: "vue--crm-e8b93.firebaseapp.com",
    databaseURL: "https://vue--crm-e8b93.firebaseio.com",
    projectId: "vue--crm-e8b93",
    storageBucket: "vue--crm-e8b93.appspot.com",
    messagingSenderId: "169796478793",
    appId: "1:169796478793:web:ebba2a5837b43900499c98"
  };

  firebase.initializeApp(firebaseConfig);

let app

firebase.auth().onAuthStateChanged(() => {//ф-ция, срабатывает неоднократно, чтобы каждый раз не перезапускать Вью
//делаем проверку на существование переменой 
	if(!app){
		app = new Vue({
		  router,
		  store,
		  render: h => h(App)
		}).$mount('#app')
		
	}
})
