import firebase from 'firebase/app';

export default{
	state:{
		info: {//если объект пустой, значит, никаких данных о пользователе у нас нет

		}
	},
	mutations: {
		setInfo(state, info){
			state.info = info
		},
		clearInfo(state){
			state.info = {}

		}
	},
	actions:{
		async fetchInfo(ctx){
			try{
				const uid = await ctx.dispatch('getUserId')
				const info = (await firebase.database().ref(`/users/${uid}/info`).once('value')).val()
				ctx.commit('setInfo', info)
				// console.log(info)
			}catch(e){
				console.log(e)

			}
		}
	},
	getters: {
		info(state){
			return state.info
		}
	}

}