<template>
	<div class="app-main-layout">
    <Navbar @click="isOpen = !isOpen"></Navbar>

    <Sidebar :value="isOpen" :key="locale"></Sidebar> <!-- :key="locale" - перерендеринг шаблона компонента, использую computed св-во. Когда меняется св-во. рендерится и шаблон  -->

    <main class="app-content" :class="{full: !isOpen}">
      <div class="app-page">
        <router-view />
      </div>
    </main>

    <div class="fixed-action-btn">
      <router-link class="btn-floating btn-large blue" to="/record" v-tooltip="'Добавить новую запись'">
        <i class="large material-icons">add</i>
      </router-link>
    </div>
  </div>
</template>

<script>
  import Sidebar from '@/components/Sidebar.vue' 
  import Navbar from '@/components/Navbar.vue' 
  export default
  {
    data(){
      return{
        isOpen: true
      }
    },
    async mounted(){//если загрузился MainLayout, значит система загружается и мы делаем запрос на наличие авторизованного пользователя.
      if(!Object.keys(this.$store.getters.info).length){//если в геттере 'info' нет никаких ключей...
        this.$store.dispatch('fetchInfo')// мы делаем запрос для получения донных о пользователе
      }

    },
    components: {
      Sidebar,
      Navbar
    },
    computed:{
      locale(){
        return this.$store.getters.info.locale
      }
    }
  }
</script>