import store from '@/store'
import ru from '@/assets/locals-data/ru-locale.json'
import en from '@/assets/locals-data/en-locale.json'
const locals = {
	'ru-RU': ru,
	 'en-US': en
}


export default function localizeFilter(value) {
	const currentLocale = store.getters.info.locale || 'ru-RU' 
	return locals[currentLocale][value];
}