<template>
	<ul class="sidenav app-sidenav" :class="{open: value}">
      <router-link
      	 v-for="item in links"
      	 tag="li"
      	 :key="item.url"
      	 active-class="active" 
      	 :to="item.url"
      	 :exact="item.exact">
        	<a href="#" class="waves-effect waves-orange pointer">{{item.title}}</a>
      </router-link>
    </ul>
</template>

<script>
import localizeFilter from '@/filters/localize.filter'
	export default {
		data() {
			return {
				links: [
					{
						title: localizeFilter('menuBill'),
						url: '/',
						exact:true
					},
					{
						title: localizeFilter('menuHistory'),
						url: '/history'
					},
					{
						title: localizeFilter('menuPlanning'),
						url: '/planning'
					},
					{
						title: localizeFilter('menuRecord'),
						url: '/record'
					},
					{
						title: localizeFilter('menuCategories'),
						url: '/categories'
					}
				]
			}
		},
		props:['value']
	}
</script>