import firebase from 'firebase/app';
export default{
	actions: {
		async updateInfo(ctx, toUpdate){//toUpdate - хранит данные, которые совпадают с теми ключами  в БД, которые нужно обновить
			try{
				const uid = await ctx.dispatch('getUserId')
				const updateData = {...ctx.getters.info, ...toUpdate} //для того, чтобы обновить info в БД нужно передать туда оба поля: name и bill
					//для этого объединяем исходный объект info с новым toUpdate
				console.log(updateData.name)
				await firebase.database().ref(`users/${uid}/info`).update(updateData) //создаем новое поле в базе данных 
				ctx.commit('setInfo', updateData) //обновляем поле info в $store
			}catch(e){
				console.log(e)
			}
		},
		async createRecord(ctx, record){
			console.log(record)
			try{
				const uid = await ctx.dispatch('getUserId')
				return await firebase.database().ref(`users/${uid}/records`).push(record) //создаем новое поле в базе данных 
			}catch(e){
				console.log(e)
			}
		},
		async fetchRecords(ctx){
			try{
				const uid = await ctx.dispatch('getUserId')
		  	const records = (await firebase.database().ref(`/users/${uid}/records`).once('value')).val() || {}
		  	return Object.keys(records).map((key) => ({...records[key], id: key}))
			}catch(e){
				ctx.commit('setError', e)
				throw e
			}
		},
		async fetchRecordsById(ctx, id){
			try{
				const uid = await ctx.dispatch('getUserId')
		  	const record = (await firebase.database().ref(`/users/${uid}/records`).child(id).once('value')).val() || {} //child(id) -- доступ к элементу в БД по его id
		  	return {...record, id}
			}catch(e){
				ctx.commit('setError', e)
				throw e
			}
		}
	}
}